$red: #ff0000;
$font: Arial;
$size: 10em;
$footer_font_color: grey;
$font_size: 16px;
$menu_size: 2 * $font-size;

body {
    color: $red;
    font-family:'Trebuchet MS'; 
}



h1 {
    background-color: lightblue;
    border-bottom: 1px solid $red;
    size: $size;
}
h3 {
    font: $font;
    font-size: 2 *  $font_size + $menu_size;
}

footer {
    color: $footer_font_color;
}
@import 'header.scss';